.header {
  max-width: 880px;
  margin: 0 auto;
  padding: 45px 0 40px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 920px) {
  .header {
    width: 100%;
    padding-top: 28px;
    padding-bottom: 31.6px;
  }
}

@media screen and (max-width: 350px){
  .header {
    max-width: 320px;
  }
}
