.popup__container {
  min-width: 430px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  background-color: #FFFFFF;
  position: relative;
}

@media screen and (max-width: 730px) {
  .popup__container {
    min-width: 282px;
  }
}

